import React, { createContext, useContext, useState, useEffect } from 'react';
import { getDailyNewsPodcast } from '../services/api';

const PodcastContext = createContext();

export const usePodcast = () => {
  const context = useContext(PodcastContext);
  if (!context) {
    throw new Error('usePodcast must be used within a PodcastProvider');
  }
  return context;
};

export const PodcastProvider = ({ children }) => {
  const [podcastData, setPodcastData] = useState(null);
  const [isLoading, setIsLoading] = useState(false);
  const [lastUpdated, setLastUpdated] = useState(null);
  const [hasInitialized, setHasInitialized] = useState(false);
  const [showConfirmDialog, setShowConfirmDialog] = useState(true);

  const fetchPodcast = async () => {
    setIsLoading(true);
    try {
      const response = await getDailyNewsPodcast();
      setPodcastData(response);
      setLastUpdated(new Date());
      setHasInitialized(true);
      setShowConfirmDialog(false);
    } catch (error) {
      console.error(':', error);
      setPodcastData({
        error: true,
        message: error.response?.data?.message || error.message || ''
      });
      setHasInitialized(true);
      setShowConfirmDialog(false);
    } finally {
      setIsLoading(false);
    }
  };

  const handleConfirmGenerate = () => {
    setShowConfirmDialog(false);
    fetchPodcast();
  };

  const handleCancelGenerate = () => {
    setShowConfirmDialog(false);
    setHasInitialized(true);
  };

  const resetPodcast = () => {
    setPodcastData(null);
    setIsLoading(false);
    setLastUpdated(null);
    setHasInitialized(false);
    setShowConfirmDialog(true);
  };

  // 
  useEffect(() => {
    // 
    const isPageReload = performance.navigation.type === 1 || 
                        (document.readyState === 'complete' && !sessionStorage.getItem('podcastInitialized'));
    
    if (isPageReload) {
      // 
      resetPodcast();
      sessionStorage.removeItem('podcastInitialized');
    } else {
      // 
      sessionStorage.setItem('podcastInitialized', 'true');
    }
  }, []);

  // sessionStorage
  useEffect(() => {
    const handleBeforeUnload = () => {
      sessionStorage.removeItem('podcastInitialized');
    };

    window.addEventListener('beforeunload', handleBeforeUnload);
    return () => {
      window.removeEventListener('beforeunload', handleBeforeUnload);
    };
  }, []);

  const value = {
    podcastData,
    isLoading,
    lastUpdated,
    hasInitialized,
    showConfirmDialog,
    fetchPodcast,
    handleConfirmGenerate,
    handleCancelGenerate,
    resetPodcast
  };

  return (
    <PodcastContext.Provider value={value}>
      {children}
    </PodcastContext.Provider>
  );
};
