import React, { useState } from 'react';
import { 
  MessageSquare, 
  Plus, 
  Trash2, 
  Clock,
  MessageCircle,
  AlertTriangle
} from 'lucide-react';
import { useChat } from '../contexts/ChatContext';

const ChatSidebar = ({ isOpen, onToggle }) => {
  const {
    conversations,
    currentConversationId,
    createConversation,
    switchConversation,
    deleteConversation,
    clearAllConversations,
    getConversationStats
  } = useChat();

  const [showDeleteConfirm, setShowDeleteConfirm] = useState(false);
  const [deleteTargetId, setDeleteTargetId] = useState(null);

  const stats = getConversationStats();

  // 
  const formatTime = (dateString) => {
    const date = new Date(dateString);
    const now = new Date();
    const diffInHours = (now - date) / (1000 * 60 * 60);
    
    if (diffInHours < 1) {
      return '';
    } else if (diffInHours < 24) {
      return `${Math.floor(diffInHours)}`;
    } else if (diffInHours < 24 * 7) {
      return `${Math.floor(diffInHours / 24)}`;
    } else {
      return date.toLocaleDateString();
    }
  };

  // 
  const handleCreateConversation = () => {
    createConversation();
  };

  // 
  const handleSwitchConversation = (conversationId) => {
    switchConversation(conversationId);
  };

  // 
  const handleDeleteConversation = (conversationId, event) => {
    event.stopPropagation();
    setDeleteTargetId(conversationId);
    setShowDeleteConfirm(true);
  };

  // 
  const confirmDelete = () => {
    if (deleteTargetId) {
      deleteConversation(deleteTargetId);
    }
    setShowDeleteConfirm(false);
    setDeleteTargetId(null);
  };

  // 
  const cancelDelete = () => {
    setShowDeleteConfirm(false);
    setDeleteTargetId(null);
  };

  // 
  const handleClearAll = () => {
    if (window.confirm('')) {
      clearAllConversations();
    }
  };

  return (
    <>
      {/*  */}
      <div className={`fixed inset-y-0 left-0 z-50 w-80 bg-white shadow-xl transform transition-transform duration-300 ease-in-out ${
        isOpen ? 'translate-x-0' : '-translate-x-full'
      } lg:relative lg:translate-x-0 lg:shadow-none`}>
        
        {/*  */}
        <div className="flex items-center justify-between p-4 border-b border-gray-200">
          <h2 className="text-lg font-semibold text-gray-800 flex items-center gap-2">
            <MessageSquare className="w-5 h-5 text-blue-500" />
            
          </h2>
          <button
            onClick={onToggle}
            className="lg:hidden p-2 text-gray-500 hover:text-gray-700 hover:bg-gray-100 rounded-md"
          >
            <Plus className="w-5 h-5 rotate-45" />
          </button>
        </div>

        {/*  */}
        <div className="p-4 bg-gray-50 border-b border-gray-200">
          <div className="flex items-center justify-between text-sm text-gray-600">
            <div className="flex items-center gap-1">
              <MessageCircle className="w-4 h-4" />
              <span>{stats.totalConversations} </span>
            </div>
            <div className="flex items-center gap-1">
              <Clock className="w-4 h-4" />
              <span>{stats.totalMessages} </span>
            </div>
          </div>
        </div>

        {/*  */}
        <div className="p-4 border-b border-gray-200">
          <button
            onClick={handleCreateConversation}
            className="w-full flex items-center justify-center gap-2 px-4 py-2 bg-blue-500 text-white rounded-lg hover:bg-blue-600 transition-colors"
          >
            <Plus className="w-4 h-4" />
            
          </button>
        </div>

        {/*  */}
        <div className="flex-1 overflow-y-auto">
          {conversations.length === 0 ? (
            <div className="p-8 text-center text-gray-500">
              <MessageSquare className="w-12 h-12 mx-auto mb-4 text-gray-300" />
              <p className="text-sm"></p>
              <p className="text-xs mt-2">""</p>
            </div>
          ) : (
            <div className="p-2">
              {conversations.map((conversation) => (
                <div
                  key={conversation.id}
                  onClick={() => handleSwitchConversation(conversation.id)}
                  className={`group relative p-3 rounded-lg cursor-pointer transition-colors ${
                    currentConversationId === conversation.id
                      ? 'bg-blue-50 border border-blue-200'
                      : 'hover:bg-gray-50'
                  }`}
                >
                  {/*  */}
                  <div className="flex items-start justify-between">
                    <div className="flex-1 min-w-0">
                      <h3 className={`text-sm font-medium truncate ${
                        currentConversationId === conversation.id
                          ? 'text-blue-700'
                          : 'text-gray-800'
                      }`}>
                        {conversation.title}
                      </h3>
                      <p className="text-xs text-gray-500 mt-1">
                        {formatTime(conversation.updatedAt)}
                      </p>
                    </div>
                    
                    {/*  */}
                    <button
                      onClick={(e) => handleDeleteConversation(conversation.id, e)}
                      className="opacity-0 group-hover:opacity-100 p-1 text-gray-400 hover:text-red-500 hover:bg-red-50 rounded transition-all"
                      title=""
                    >
                      <Trash2 className="w-4 h-4" />
                    </button>
                  </div>
                  
                  {/*  */}
                  {conversation.messageCount > 0 && (
                    <div className="flex items-center gap-1 mt-2">
                      <MessageCircle className="w-3 h-3 text-gray-400" />
                      <span className="text-xs text-gray-500">
                        {conversation.messageCount} 
                      </span>
                    </div>
                  )}
                </div>
              ))}
            </div>
          )}
        </div>

        {/*  */}
        {conversations.length > 0 && (
          <div className="p-4 border-t border-gray-200">
            <button
              onClick={handleClearAll}
              className="w-full flex items-center justify-center gap-2 px-3 py-2 text-sm text-red-600 hover:text-red-700 hover:bg-red-50 rounded-lg transition-colors"
            >
              <AlertTriangle className="w-4 h-4" />
              
            </button>
          </div>
        )}
      </div>

      {/*  */}
      {showDeleteConfirm && (
        <div className="fixed inset-0 z-50 flex items-center justify-center bg-black bg-opacity-50">
          <div className="bg-white rounded-lg p-6 max-w-sm w-full mx-4">
            <div className="flex items-center gap-3 mb-4">
              <AlertTriangle className="w-6 h-6 text-red-500" />
              <h3 className="text-lg font-semibold text-gray-800"></h3>
            </div>
            <p className="text-gray-600 mb-6">
              
            </p>
            <div className="flex gap-3">
              <button
                onClick={cancelDelete}
                className="flex-1 px-4 py-2 text-gray-600 bg-gray-100 rounded-lg hover:bg-gray-200 transition-colors"
              >
                
              </button>
              <button
                onClick={confirmDelete}
                className="flex-1 px-4 py-2 text-white bg-red-500 rounded-lg hover:bg-red-600 transition-colors"
              >
                
              </button>
            </div>
          </div>
        </div>
      )}

      {/*  */}
      {isOpen && (
        <div 
          className="fixed inset-0 z-40 bg-black bg-opacity-50 lg:hidden"
          onClick={onToggle}
        />
      )}
    </>
  );
};

export default ChatSidebar;
